import { ActionContext, createLogger, createStore } from "vuex";
import { State } from "@/interfaces/State";
import appVersion from "@/util/version";
import editorStoreModule from "@/modules/Editor/storeModule";
import authStoreModule from "@/auth/storeModule";

const STORAGE_KEY = "boto";
const LOCALE_KEY = "boto-locale";
// const LATEST_BOT_KEY = "boto-latest-bot";
const APP_VERSION_KEY = "boto-version";
const defaultLocale = "en";

const state: State = {
  locale: localStorage.getItem(LOCALE_KEY) || defaultLocale,
  appVersion: localStorage.getItem(APP_VERSION_KEY) || "",
};
const getters = {};
const actions = {
  initialize: (context: ActionContext<State, any>, initialState: State) => {
    const localVersion = localStorage.getItem(APP_VERSION_KEY);
    const localCfg = localStorage.getItem(STORAGE_KEY);
    if (!localVersion || localVersion !== appVersion || !localCfg) {
      context.commit("initialize", initialState);
      localStorage.setItem(STORAGE_KEY, JSON.stringify(initialState));
      localStorage.setItem(APP_VERSION_KEY, appVersion);
    }
  },
  saveLocale: (context: ActionContext<State, any>, locale: string) => {
    context.commit("setLocale", locale);
    localStorage.setItem(LOCALE_KEY, locale);
  },
};
const mutations = {
  initialize: (state: State, initialState: State) => {
    state = { ...initialState };
  },
  setLocale: (state: State, locale: string) => {
    state.locale = locale;
  },
};
const plugins = [
  createLogger({
    logActions: process.env.NODE_ENV === "development",
    logMutations: process.env.NODE_ENV === "development",
  }),
];

const store = createStore({
  strict: process.env.NODE_ENV === "development",
  modules: {
    editor: editorStoreModule,
    auth: authStoreModule,
  },
  plugins,
  state,
  getters,
  actions,
  mutations,
});

export default store;
