import { Module, ActionContext } from "vuex";
import { Boto } from "@/interfaces/Boto";

const TEMPLATE_KEY = "boto-template";
export interface EditorState {
  template?: Boto;
}

const state: EditorState = {
  template: JSON.parse(localStorage.getItem(TEMPLATE_KEY) || "{}") as Boto,
};

const getters = {};
const actions = {
  setTemplate: (context: ActionContext<EditorState, any>, template: Boto) => {
    /**
     * Possible approach
     * gets bot from either recipes or duplicate approach
     * also, if user reloads editor, data should come from url id paramter.
     */
    console.log("setTemplate Action");
    context.commit("setTemplate", template);
  },
  setTemplateLocalStorage: (
    context: ActionContext<EditorState, any>,
    template: Boto
  ) => {
    /** USE CASE:
     * When navigating from the Home page editor
     */
    localStorage.setItem(TEMPLATE_KEY, JSON.stringify(template));
  },
  removeTemplateLocalStorage: () => {
    localStorage.removeItem(TEMPLATE_KEY);
  },
};
const mutations = {
  setTemplate: (state: EditorState, template: Boto) => {
    state.template = template;
  },
};

const editorStoreModule: Module<EditorState, any> = {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};

export default editorStoreModule;
