import { Module, ActionContext } from "vuex";

const AUTH_TOKEN_KEY = "boto-auth-token";

export interface AuthState {
  authToken?: string;
}

const state: AuthState = {
  authToken: localStorage.getItem(AUTH_TOKEN_KEY) || "",
};

const getters = {};
const actions = {
  setAuthToken: (context: ActionContext<AuthState, any>, authToken: string) => {
    context.commit("setAuthToken", authToken);
  },
  removeLocalStorageAuthToken: () => {
    localStorage.removeItem(AUTH_TOKEN_KEY);
  },
  setLocalStorageAuthToken: (
    context: ActionContext<AuthState, any>,
    authToken: string
  ) => {
    localStorage.setItem(AUTH_TOKEN_KEY, authToken);
  },
};
const mutations = {
  setAuthToken: (state: AuthState, authToken: string) => {
    state.authToken = authToken;
  },
};

const authStoreModule: Module<AuthState, any> = {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};

export default authStoreModule;
