import createAuth0Client, {
  Auth0ClientOptions,
  User,
} from "@auth0/auth0-spa-js";
import { computed, ComputedRef, reactive, watchEffect } from "vue";

let client: any;

const state = reactive({
  loading: true,
  isAuthenticated: false,
  user: {},
  popupOpen: false,
  error: null,
});

export const getInstance = () => authPlugin;

async function loginWithPopup() {
  state.popupOpen = true;

  try {
    await client.loginWithPopup(0);
  } catch (e) {
    console.error(e);
  } finally {
    state.popupOpen = false;
  }

  state.user = await client.getUser();

  state.isAuthenticated = true;
}

async function handleRedirectCallback() {
  state.loading = true;
  try {
    await client.handleRedirectCallback();
    state.user = await client.getUser();
    console.dir(state.user);
    state.isAuthenticated = true;
  } catch (e) {
    (state as any).error = e;
  } finally {
    state.loading = false;
  }
}

function loginWithRedirect(o?: any) {
  return client.loginWithRedirect(o);
}

function getIdTokenClaims(o: any) {
  return client.getIdTokenClaims(o);
}

async function getTokenSilently() {
  return await client.getTokenSilently();
}

function getTokenWithPopup(o: any) {
  return client.getTokenWithPopup(o);
}

function logout(o: any) {
  return client.logout(o);
}

export interface Auth {
  sub: string;
  isAuthenticated: ComputedRef<boolean>;
  loading: ComputedRef<boolean>;
  user: ComputedRef<User>;
  error: ComputedRef<boolean | null>;
  getIdTokenClaims: (o: any) => any;
  getTokenSilently: () => any;
  getTokenWithPopup: (o: any) => any;
  handleRedirectCallback: () => any;
  loginWithRedirect: (o?: any) => any;
  loginWithPopup: () => any;
  logout: (o: any) => any;
}

const authPlugin: Auth = {
  sub: "",
  isAuthenticated: computed(() => state.isAuthenticated),
  loading: computed(() => state.loading),
  user: computed(() => state.user),
  error: computed(() => state.error),
  getIdTokenClaims,
  getTokenSilently,
  getTokenWithPopup,
  handleRedirectCallback,
  loginWithRedirect,
  loginWithPopup,
  logout,
};

export const functionAuthGuard = function (
  func: (...args: any[]) => any,
  token?: string
): (...args: any[]) => any {
  const { isAuthenticated, loading, loginWithRedirect } = authPlugin;
  return (...args: any[]) => {
    if (isAuthenticated.value || token) func(...args);
    else loginWithRedirect();
  };
};
// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
export const routeGuard = (
  to: { fullPath: unknown },
  _from: unknown,
  next: () => unknown
) => {
  const { isAuthenticated, loading, loginWithRedirect } = authPlugin;

  const verify = () => {
    // If the user is authenticated, continue with the route
    if (isAuthenticated.value) {
      return next();
    }

    // Otherwise, log in
    loginWithRedirect({ appState: { targetUrl: to.fullPath } });
  };

  // If loading has already finished, check our auth state using `fn()`
  if (!loading.value) {
    return verify();
  }

  // Watch for the loading property to change before we check isAuthenticated
  watchEffect(() => {
    if (loading.value === false) {
      return verify();
    }
  });
};

export const setupAuth = async (
  options: Auth0ClientOptions,
  callbackRedirect: (arg0: any) => void
) => {
  try {
    client = await createAuth0Client({
      ...options,
    });
    // If the user is returning to the app after authentication
    if (
      window.location.search.includes("code=") &&
      window.location.search.includes("state=")
    ) {
      // handle the redirect and retrieve tokens
      const st = await client.handleRedirectCallback();
      // Notify subscribers that the redirect callback has happened, passing the appState
      // (useful for retrieving any pre-authentication state)
      callbackRedirect(st.appState);
    }
  } catch (e) {
    (state as any).error = e;
  } finally {
    if (client) {
      // Initialize our internal authentication state
      state.isAuthenticated = await client.isAuthenticated();
      state.user = await client.getUser();
    }
    state.loading = false;
  }
  return {
    install: (app: any) => {
      app.config.globalProperties.$auth = authPlugin;
    },
  };
};
